<script setup>
import { ref, watchEffect } from "vue";
// import { ref, watchEffect, onMounted, onBeforeMount } from "vue";
import { useRoute } from "vue-router";
import { useGlobalStore } from "@/stores/globalStore";
import html2canvas from "html2canvas";

const route = useRoute();
const { publicDir } = useGlobalStore();
const domain = ref(route.params.domain || "scb");
const uidn = ref(route.params.uidn || ""); 
const capture = ref(null);
const image = ref(null);


watchEffect(() => {
  uidn.value = route.params.uidn || "";
});

const captureDiv = () => {
  if (!capture.value) return;

  // Select the elements to hide
  const buttonsToHide = document.querySelectorAll(".visitor_button");

  // Hide the buttons
  buttonsToHide.forEach((button) => (button.style.display = "none"));

  html2canvas(capture.value, { useCORS: true, allowTaint: true }).then((canvas) => {
    // Restore the buttons
    buttonsToHide.forEach((button) => (button.style.display = "flex"));

    const imageData = canvas.toDataURL("image/png");
    image.value = imageData;

    const link = document.createElement("a");
    link.href = imageData;
    link.download = `meeting-${domain.value}.png`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  });
};
</script>

<template>
  <section class="background-gradi request-meeting test12333 visitor_del">
    <div class="container">
      <div class="row align-items-center">
        <div class="col col-12 col-md-12 col-sm-12">
          <div class="popup-logo with_scan_cls" ref="capture">

            <router-link to="/">
              <img
                :src="`${publicDir}/qrcode/qrcode-${uidn}.png`"
                class=""
                alt="secure-check-logo"
              />
            </router-link>

            <div class="text-center py-2">
              <h3>{{ uidn }}</h3>
            </div>
            <div class="request-btn capture-button" @click="captureDiv" role="button">
              <a>Capture</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<style scoped>
.request-btn-cannel {
  margin: 10px auto 0px auto;
  display: inline-block;
  text-align: center;
  width: 100%;
}
.request-btn-cannel a {
  background: #b92b00;
  color: #ffffff;
  padding: 20px 100px;
  font-weight: 600;
  overflow: hidden;
  margin: 0 auto;
  float: left;
  width: 100%;
  font-size: 20px;
}
.request-btn a {
  color: #ffffff;
  border-radius: 10px;
  padding: 20px 100px;
  font-weight: 600;
  overflow: hidden;
  margin: 0 auto;
  float: left;
  width: 100%;
  font-size: 20px;
}
.request-btn-cannel a:hover {
  background: #000000;
  color: #ffffff;
}
.popup-logo.with_scan_cls {
  background: #37bbf0 !important;
  position: unset !important;
  transform: unset !important;
  padding: 40px 20px !important;
  border-radius: 15px;
}
.popup-logo.with_scan_cls img {
  border-radius: 12px;
  margin-top: 20px;
}
</style>
