<script setup>
import { ref, computed, onMounted } from "vue";
import { useRequestsList } from "@/services/useRequestsList";
import { useRoute, useRouter } from "vue-router";
import { useEvent } from "@/services/useEvent";
import { useUserStore } from "@/stores/useUserStore";

const { requests, error, fetchRequestsByComp, fetchRequests } = useRequestsList();
const router = useRouter();
const route = useRoute();
const userStore = useUserStore();


const formatDate = (dateString) => {
  const date = new Date(dateString);
  return date.toLocaleDateString("fr-FR", {
    year: "numeric",
    month: "long",
    day: "numeric",
    hour: "numeric",
    minute: "numeric",
  });
};

const goToMenu = () => {
  router.push({ name: "Menu", params: { domain: domain.value } });
};

const currentPage = ref(1);
const itemsPerPage = ref(10);

const roles = ref(JSON.parse(localStorage.getItem("userInfo")) || {});
const currentRole = ref(roles?.value?.roles ? roles.value.roles[0] : "");

const tableHeaders = computed(() => {
  const headers = ["Visiteur", "Email", "Contact", "Host", "Statut", "Date Cration"];
  if (currentRole.value !== "ROLE_SUPERVISOR") {
    headers.push("Action");
  }
  return headers;
});

const tableBody = computed(() => {
  const body = [
    { slot: "visitor" },
    { slot: "email" },
    { slot: "contact" },
    "host",
    { slot: "confirmed" },
    { slot: "request_date" },
  ];
  if (currentRole.value !== "ROLE_SUPERVISOR") {
    body.push({ slot: "actions" });
  }
  return body;
});

const show = (id, address, request_time , request_date ,organisationName, city, state, zipcode, country ) => {
  // router.push(`/waiting-validation/${id}`);
  // alert(id);
  // alert(domain.value);

  router.push({
    name: "WaitingValidation",
    params: {
      domain: domain.value,
      id: id,
    },
    state: { 
      address: address,
      request_time: request_time,
      request_date: request_date,
      organisationName: organisationName,
      city: city,
      state: state,
      zipcode: zipcode,
      country: country,
      
     },
  });
};

const paginatedRequests = computed(() => {
  const start = (currentPage.value - 1) * itemsPerPage.value;
  const end = start + itemsPerPage.value;
  return route.params.slug
    ? filteredRequests.value.slice(start, end)
    : requests.value.slice(start, end);
});
console.log('requestid234344',paginatedRequests);
const { showEvent, event } = useEvent();
const filteredRequests = computed(() => {
  return requests.value.filter(
    (request) => request.visitor.evenements?.id === event?.value?.id
  );
});

const domain = ref(route.params.domain || "scb");
onMounted(async () => {
  if (route?.params?.slug) {
    await showEvent(route?.params?.slug);
  }

  if (!userStore.isAdmin(currentRole.value)) {
    await fetchRequestsByComp(domain.value);
  } else {
    await fetchRequests();
  }

  requests.value = filteredRequests.value;
});
</script>

<template>
  <div class="container">
    <div class="d-flex justify-content-center align-items-center">
      <div class="d-flex justify-content-start mb-4 gap-3 align-items-center">
        <button class="back" @click="goToMenu">Retour</button>
        <h3 class="mt-3" v-if="route?.params?.slug">Liste Participants</h3>
        <h3 class="mt-3" v-else>Liste des demandes</h3>
      </div>
    </div>

    <div class="text-center">
      <h4 v-if="route?.params?.slug">Evenements : {{ event?.name }}</h4>
    </div>

    <div class="d-flex justify-content-center py-3">
      <ui-table
        :data="paginatedRequests"
        :thead="tableHeaders"
        :tbody="tableBody"
      >
        <template #visitor="{ data }">
          {{ data.visitor.firstname }} {{ data.visitor.lastname }}
        </template>

        <template #email="{ data }">
          {{ data.visitor.email }}
        </template>

        <template #contact="{ data }">
          {{ data.visitor.contact }}
        </template>

        <template #request_date="{ data }">
          {{ formatDate(data.request_date) }}
        </template>

        <template #confirmed="{ data }">
          {{ data.confirmed ? "Confirm" : "En attente" }}
        </template>
        <template #actions="{ data }" v-if="currentRole !== 'ROLE_SUPERVISOR'" >
          <ui-icon v-if="currentRole !== 'ROLE_SUPERVISOR' && data.confirmed == true && data.status == true " role="button" @click="show(data.id , data.visitor.address , data.visitor.request_time , data.visitor.request_date , data.visitor.organisationName,data.visitor.city, data.visitor.state, data.visitor.zipcode,  data.visitor.country )"> edit </ui-icon>
        </template>
      </ui-table>
    </div>

    <ui-pagination
      v-if="!route?.params?.slug"
      v-model="currentPage"
      :total="requests.length"
      show-total
      @update:model-value="onPage"
    ></ui-pagination>

    <div v-if="error">{{ error }}</div>
  </div>
</template>

<style scoped>
.pagination {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
button {
  margin: 0 10px;
  padding: 5px 10px;
}
</style>