<script>
import myService from "@/services/visitorcode"; // Adjust the path based on your project structure
import { ref, onMounted } from "vue";
import { useRoute, useRouter } from "vue-router";
import { useUserStore } from "@/stores/useUserStore";

export default {
  name: "UserAttendenceView",
  setup() {
    const router = useRouter();
    const route = useRoute();
    const domain = ref(route?.params?.domain || "scb");
    const userLogs = ref([]);
    const message = ref("");
    const error = ref(null);
    const userStore = useUserStore();

    // Function to fetch event data
    const fetchUserData = async () => {
      try {
        const response = await myService.fetchUserLog(); // Fetch the data from the service
        userLogs.value = response; 
        message.value = "Data fetched successfully!";
      } catch (err) {
        error.value = "Failed to fetch data.";
        console.error(err);
      }
    };

    // Function to navigate to Menu page
    const goToMenu = () => {
      router.push({
        name: "Menu",
        params: {
          domain: domain.value,
        },
      });
    };

    // Fetch event data when the component is mounted
    onMounted(() => {
        fetchUserData();
    });

console.log("fdhbgbhdfgbdfhgbfgjfdgfdgfhgfhj=========",userLogs)

    return {
      domain,
      userLogs,
      message,
      error,
      goToMenu,
      userStore,
    };
  },
};
</script>

<template>
  <div class="container">
    <!-- Back Button and Header -->
    <div class="d-flex justify-content-center align-items-center">
      <div class="d-flex justify-content-start mb-4 gap-3 align-items-center">
        <button class="back" @click="goToMenu()">Retour</button>
        <h3 class="mt-3">Lista de Check in</h3>
      </div>
    </div>

    

    <!-- Visitor Logs Table -->
    <div class="d-flex justify-content-center py-3">
      <table class="table table-striped">
        <thead>
          <tr>
            <th>User Id</th>
            <th>User Name</th>
            <th>User Email</th>
            <th>Check In</th>
            <th>Check Out</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          <tr v-if="!userLogs || userLogs.length === 0">
            <td colspan="6" class="text-center">Data not found</td>
          </tr>
          <tr v-else v-for="log in userLogs" :key="log.id">
            <td>{{ log.user_id }}</td>
            <td>{{ log.user_name }}</td>
            <td>{{ log.user_email }} {{ log.id }}</td>
            <td>{{ log.check_in_time }}</td>
            <td>{{ log.check_out_time }}</td>
            <td >
              <router-link :to="{ name: 'UserQrCodeDisable', params: { id: log.user_id } }">Edit</router-link>
          </td>
          </tr>
        </tbody>
        <tbody>
        </tbody>
      </table>
    </div>
  </div>
</template>
